import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import {GraphComponent} from '../app/graph/graph.component'
import {DashboardComponent} from '../app/dashboard/dashboard.component'
import {MainComponent} from '../app/main/main.component';
import {DetailComponent} from '../app/detail/detail.component';
import {SelecComponent} from '../app/selec/selec.component';
import {LoginComponent} from '../app/login/login.component';
import {SelecmiterComponent} from '../app/selecmiter/selecmiter.component'
import {HistoryComponent} from '../app/history/history.component'
import {GraphfourComponent} from '../app/graphfour/graphfour.component'
import {LogfourComponent} from '../app/logfour/logfour.component'
import {DetailenviComponent} from './detailenvi/detailenvi.component'
import {EnviromentComponent} from './enviroment/enviroment.component'

const routes: Routes = [
   {path:'',component:MainComponent},
   {path:'dashboard',component:DashboardComponent},
   {path:'detail',component:DetailComponent},
   {path:'graph',component:GraphComponent},
   {path:'selec',component:SelecComponent},
   {path:'login',component:LoginComponent},
   {path: 'selecm',component:SelecmiterComponent},
   {path: 'history',component:HistoryComponent},
   {path: 'logfour',component:LogfourComponent },
   {path: 'graphfour',component:GraphfourComponent},
   {path: 'detailen',component: DetailenviComponent},
   {path: 'envi',component: EnviromentComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
