 convertToCSV(objArray) {
  var array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
  var str = '';

  for (var i = 0; i < array.length; i++) {
      var line = '';
      for (var index in array[i]) {
          if (line != '') line += ','

          line += array[i][index];
      }

      str += line + '\r\n';
  }

  return str;
}

 exportCSVFile(headers, items, fileTitle) {
  if (headers) {
      items.unshift(headers);
  }

  // Convert Object to JSON
  var jsonObject = JSON.stringify(items);

  var csv = this.convertToCSV(jsonObject);

  var exportedFilenmae = fileTitle + '.csv' || 'export.csv';

  var blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
  if (navigator.msSaveBlob) { // IE 10+
      navigator.msSaveBlob(blob, exportedFilenmae);
  } else {
      var link = document.createElement("a");
      if (link.download !== undefined) { // feature detection
          // Browsers that support HTML5 download attribute
          var url = URL.createObjectURL(blob);
          link.setAttribute("href", url);
          link.setAttribute("download", exportedFilenmae);
          link.style.visibility = 'hidden';
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
      }
  }
}



 exportCsv(){
  var header = {
      DateTime: 'DateTime',
      Humidity: 'Humidity',
      Pyranometer: 'Solar Irradiance',
      Temperature: 'Temperature',
      Frequency: 'Frequency',
  }
  var itemNotFormat = this.item
  var datee = this.date
  console.log(datee)
  var year = datee.getFullYear()
  var dat = datee.getDate()
  if (dat < 10) {
    dat = "0" + dat
  }
  var month = datee.getMonth() + 1
  console.log(month)
  if (month < 10) {
    month = "0" + month
  }
  var fileTitle = String(year) + String(month) + String(dat) + "_env_data"
  // var  'solar';
  var itemFomat = []
  itemNotFormat.forEach((item) =>{
    itemFomat.push({
      DateTime: item['result'][0]['DateTime'],
      Humidity: item['result'][0]['Humidity'],
      Pyranometer: item['result'][0]['Pyranometer'],
      Temperature: item['result'][0]['Temperature'],
      Frequency: item['result'][0]['Frequency'],
    })
  })
  this.exportCSVFile(header, itemFomat, fileTitle);
}

