import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Observer, throwError, Observable } from 'rxjs'
import * as socketIo from 'socket.io-client'
import { catchError, retry, delay } from 'rxjs/operators'

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { }

  observer: Observer<any>;

  getData(uri): Observable<any> {
    return this.http.get<any>(uri).pipe(delay(1000),
      retry(1), catchError(this.handleError));
  }

  postData(uri,data): Observable<any> {
    return this.http.post<any>(uri,data).pipe(delay(1000),
      retry(1), catchError(this.handleError));
  }

  public getsocketdata = (da) => {
    // const socket = socketIo('http://167.71.220.10:2000');
    const socket = socketIo('http://www.mesfia.eng.nu.ac.th/')
    socket.on(da, response => {
      return this.observer.next(response);
    });
    return this.createObservable();
  } 

  createObservable() {
    return new Observable(observer => this.observer = observer);
  }
  
  handleError(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    // window.alert(errorMessage);
    return throwError(errorMessage);
  }
}
