  test(i){
    console.log(i)
    localStorage.setItem('meter',this.main + i)
    if (i == "1") {
      // console.log("true")
      localStorage.setItem('namemeter',"Incoming/Main")
      localStorage.setItem('detail',JSON.stringify({
        detail: ["5 feeders","3 phase Grid tied PV system : 1 unit","1 phase Hybrid PV-Battery system : 1 unit"]
      }))
    }else if(i == "2"){
      localStorage.setItem('namemeter',"Feeder 01")
      localStorage.setItem('detail',JSON.stringify({
        detail: ["Air condition system (3 phase and 1 phase)","Outdoor lighting","Workshop"]
      }))
    }else if(i == "3"){
      localStorage.setItem('namemeter',"Feeder 02")
      localStorage.setItem('detail',JSON.stringify({
        detail: ["Water Supply and Sanitary system"]
      }))
    }else if(i == "4"){
      localStorage.setItem('namemeter',"Feeder 03")
      localStorage.setItem("detail",JSON.stringify({
        detail: ["Air condition system (3 phase)","Caf and working space"]
      }))
    }else if(i == "5"){
      localStorage.setItem('namemeter',"Feeder 04")
      localStorage.setItem('detail',JSON.stringify({
        detail: ["Energy sources: 5 kW Grid tied PV and 2.5 kW Hybrid PV- Battery systems","Unknown loads"]
      }))
    }else if(i == "6"){
      localStorage.setItem('namemeter',"Feeder 05")
      localStorage.setItem('detail',JSON.stringify({
        detail: ["Electrical and Control room"]
      }))
    }else if(i == "7"){
      localStorage.setItem('namemeter',"On grid PV systems")
      localStorage.setItem('detail',JSON.stringify({
        detail: ["3 phase 5 kW Grid-tied PV system","1 phase 2.5 kW Hybrid PV-Battery system (Phase 1)"]
      }))
    }else if(i == "8"){
      localStorage.setItem('namemeter',"Hybrid PV system")
      localStorage.setItem('detail',JSON.stringify({
        detail: ["1 phase 2.5 kW Hybrid PV-Battery system : phase 1 is on-grid and phase 2 is emergency"]
      }))
    }else if(i == "9"){
      localStorage.setItem('namemeter',"")
    }
    this.router.navigate(["detail"])
  }

