bubble_Sort(a)
{
  
    var swapp;
    var n = a.length-1;
    var x=a;
    
    for (var j=0; j < n; j++){
      var test = a[j]['result'][0]['DateTime'].split(" ")
      var date = test[0].split("/")
      var time = test[1].split(":")
      console.log(a[j]['result'][0]['DateTime'])
      var ss = new Date(date[2],date[1],date[0],time[0],time[1],time[2])
      console.log(ss)
    }
    // var ss = new Date(a[1]['result'][0]['DateTime'])
    // console.log(ss)
    do {
        swapp = false;
        for (var i=0; i < n; i++)
        {
            var test = x[i]['result'][0]['DateTime'].split(" ")
            var test2 = x[i+1]['result'][0]['DateTime'].split(" ")
            var date = test[0].split("/")
            var time = test[1].split(":")
            var date2 = test2[0].split("/")
            let time2 = test2[1].split(":")

            let first = new Date(date[2],date[1],date[0],time[0],time[1],time[2])
            let second = new Date(date2[2],date2[1],date2[0],time2[0],time2[1],time2[2])
            
            if (first > second)
            {
              var temp = x[i];
              x[i] = x[i+1];
              x[i+1] = temp;
              swapp = true;
            }
        }
        n--;
    } while (swapp);
    return x; 
}



