import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import {HttpClient} from '@angular/common/http'
import { Router } from '@angular/router'
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {

  item: Array<any>;
  pici: Array<any>;
  loading = true;
  constructor(private api: ApiService,private route: Router) { }

  ngOnInit(): void {
    this.api.getData('http://www.mesfia.eng.nu.ac.th/building').subscribe(async (data)=>{
      // let build = []
      let name = []
      let pic = []
      for (const iterator of data) {
        // build.push(iterator)
        if (iterator == "USIS") {
          name.push("USIS")
          pic.push("../../assets/1569482799604.jpg")
        } else {
          name.push("NONE")
          pic.push('') 
        }
      }
      this.pici = await pic;
      this.item = await name;
      this.loading = false;
      console.log(this.item)
      console.log(this.pici)
    })    
  }

  test(i){
    // console.log(this.route.getCurrentNavigation())
    localStorage.setItem('name',JSON.stringify({name: i}))
    this.route.navigate(['selecm'])
    console.log(this.item)
  }

}
