  addDate(event: MatDatepickerInputEvent<Date>) {
    this.date = event.value;
    console.log(this.date.getDate())
    console.log(this.name)
    let postda = {
      building: this.name,
      block: 1,
      date: this.date.getDate(),
      month: this.date.getMonth() + 1,
      year: this.date.getFullYear() + 543,
    }
    this.api.postData('http://www.mesfia.eng.nu.ac.th/history',postda).subscribe(async (data)=>{
      // var dae = data[0]['create']
      var a = []
      var check = 0
      console.log(data)
      // var er = new Date(dae)
      // console.log(data["355"])
      for (const iterator in data) {
        var dae = data[iterator]['create']
        var er = new Date(dae)
        // console.log(iterator)
        if (iterator == "0") {
          a.push(data[iterator])
          console.log(er.getHours())
        } else if(er.getMinutes() >= 26 && er.getMinutes() <= 34) {
          a.push(data[iterator])
        }
      }
      console.log(a)
      this.item = a
      this.graph(this.item)
      // console.log(data)
    })
    
  }

