import { Component, OnInit } from '@angular/core';
import { Chart } from 'chart.js';
import { Router, Routes } from '@angular/router'
import { ApiService } from '../api.service'
import * as socketIo from 'socket.io-client'

@Component({
   selector: 'app-detail',
   templateUrl: './detail.component.html',
   styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

   main;
   name;
   num;
   now;
   namemeter;
   dae
   energy;
   mainn;
   detail;
   constructor(private route: Router, private api: ApiService) {
      try {
         this.dae = JSON.parse(localStorage.getItem('name'))
         this.mainn = this.dae.name
         this.main = localStorage.getItem('meter')
         this.namemeter = localStorage.getItem('namemeter')
         this.name = this.main.slice(0, this.main.length - 1)
         this.num = parseInt(this.main.slice(this.main.length-1,this.main.length))
         this.detail = JSON.parse(localStorage.getItem('detail'))
         console.log(this.detail)
      } catch (error) {
         console.log(error)
         this.route.navigate(['/'])
      }
      setInterval(() => {
         var wnow = new Date()
         this.now = wnow.toLocaleString()
      })
   }

   CV = []
   CC = []
   CP = []
   CPF = []
   CVA = []
   CVAR = []
   ChartV: any = []; //
   ChartC: any = [];
   ChartP: any = [];
   ChartVA: any = [];
   ChartVAR: any = [];
   ChartPF: any = [];
   boolVolt = true;
   boolCur = false;
   boolPow = false;
   boolVA = false;
   boolVAR = false;
   boolPF = false;
   Volt = [];
   Current = [];
   Power = [];
   VA = [];
   VAR = [];
   powertotal;
   PF = [];
   freq;
   pyra;
   tem;
   hum;
   block;
   datee = []
   ct = "lineChart"
   cx = "lineChartC"
   navbarOpen = false;

  toggleNavbar() {
    this.navbarOpen = !this.navbarOpen;
  }

   ngOnInit() {
      console.log(this.main)
      this.getData();
      console.log(this.freq)
   }

   // home(){
   //    this.route.navigate(['/'])
   //  }

   delay(ms: number) {
      return new Promise(resolve => setTimeout(resolve, ms));
   }

   pass(){
      this.route.navigate(["selecm"])
   }

   graph(){
      this.route.navigate(['/graph'])
   }

   history(){
      this.route.navigate(['/history'])
   }

   async getData() {
      this.api.getsocketdata(this.main.toLowerCase()).subscribe(async (result: any) => {
         let dat = new Date();
         this.Volt = [];
         this.Current = [];
         this.Power = [];
         this.VA = [];
         this.VAR = [];
         this.PF = [];
         this.block = parseInt(result.data[0]['block']);
         console.log(result);
         await this.Volt.push(result.data[0].result[0]["Volt_1"]);
         await this.Volt.push(result.data[0].result[0]["Volt_2"]);
         await this.Volt.push(result.data[0].result[0]["Volt_3"]);
         await this.Current.push(result.data[0].result[0]["Current_1"]);
         await this.Current.push(result.data[0].result[0]["Current_2"]);
         await this.Current.push(result.data[0].result[0]["Current_3"]);
         await this.Power.push(result.data[0].result[0]["Power_1"]);
         await this.Power.push(result.data[0].result[0]["Power_2"]);
         await this.Power.push(result.data[0].result[0]["Power_3"]);
         await this.VA.push(result.data[0].result[0]["VA_1"]);
         await this.VA.push(result.data[0].result[0]["VA_2"]);
         await this.VA.push(result.data[0].result[0]["VA_3"]);
         await this.VAR.push(result.data[0].result[0]["VAR_1"]);
         await this.VAR.push(result.data[0].result[0]["VAR_2"]);
         await this.VAR.push(result.data[0].result[0]["VAR_3"]);
         await this.PF.push(result.data[0].result[0]["PF_1"]);
         await this.PF.push(result.data[0].result[0]["PF_2"]);
         await this.PF.push(result.data[0].result[0]["PF_3"]);
         this.powertotal = result.data[0].result[0]["PowerSum"];
         var a = result.data[0].result[0]["Energy_Ex"];
         this.energy = a;
         this.freq = await result.data[0].result[0]["Frequency"];
         this.pyra = await result.data[0].result[0]["Pyranometer"];
         this.tem = await result.data[0].result[0]["Temperature"];
         this.hum = await result.data[0].result[0]["Humidity"];
         console.log(this.Volt);
         console.log(this.CV);
         console.log(this.datee);
      });
   }

   test(i){
      console.log(i)
      localStorage.setItem('meter',this.mainn + i)
      if (i == "1") {
        // console.log("true")
        localStorage.setItem('namemeter',"Incoming/Main")
        localStorage.setItem('detail',JSON.stringify({
          detail: ["5 feeders","3 phase Grid tied PV system : 1 unit","1 phase Hybrid PV-Battery system : 1 unit"]
        }))
      }else if(i == "2"){
        localStorage.setItem('namemeter',"Feeder 01")
        localStorage.setItem('detail',JSON.stringify({
          detail: ["Air condition system (3 phase and 1 phase)","Outdoor lighting","Workshop"]
        }))
      }else if(i == "3"){
        localStorage.setItem('namemeter',"Feeder 02")
        localStorage.setItem('detail',JSON.stringify({
          detail: ["Water Supply and Sanitary system"]
        }))
      }else if(i == "4"){
        localStorage.setItem('namemeter',"Feeder 03")
        localStorage.setItem("detail",JSON.stringify({
          detail: ["Air condition system (3 phase)","Caf and working space"]
        }))
      }else if(i == "5"){
        localStorage.setItem('namemeter',"Feeder 04")
        localStorage.setItem('detail',JSON.stringify({
          detail: ["Energy sources: 5 kW Grid tied PV and 2.5 kW Hybrid PV- Battery systems","Unknown loads"]
        }))
      }else if(i == "6"){
        localStorage.setItem('namemeter',"Feeder 05")
        localStorage.setItem('detail',JSON.stringify({
          detail: ["Electrical and Control room"]
        }))
      }else if(i == "7"){
        localStorage.setItem('namemeter',"On grid PV systems")
        localStorage.setItem('detail',JSON.stringify({
          detail: ["3 phase 5 kW Grid-tied PV system","1 phase 2.5 kW Hybrid PV-Battery system (Phase 1)"]
        }))
      }else if(i == "8"){
        localStorage.setItem('namemeter',"Hybrid PV system")
        localStorage.setItem('detail',JSON.stringify({
          detail: ["1 phase 2.5 kW Hybrid PV-Battery system : phase 1 is on-grid and phase 2 is emergency"]
        }))
      }else if(i == "9"){
        localStorage.setItem('namemeter',"")
      }
      window.location.reload();

      this.route.navigate(["detail"])
    }
}
